














Curve = require './components/Curve'

jstat = require 'jstat'

#[alpha, beta] = [1, 1]
#[alpha, beta] = [.7, 5]
#[alpha, beta] = [5, 73]

module.exports =
  name: 'app',
  components: {Curve}
  data: () ->
    alpha: 1.53
    beta: .73

  computed:
      values: () ->
          (jstat.jStat.beta.pdf(x/100, @alpha, @beta) for x in [0...100])




